<template>
  <div class="data-box">
    <!-- 公共头部 -->
    <div class="public-header">
      <div class="center-pos">
        <div class="border-left">
          <dv-decoration-8 :reverse="true" style="width: 70rem; height: 5rem" />
        </div>
        <div class="text-center">{{ $window.dataViewTitle }}</div>
        <div class="border-right">
          <dv-decoration-8 style="width: 70rem; height: 5rem" />
        </div>
        <div class="tabs-left">
          <div
            class="tab-item"
            v-for="(item, index) in tabsData"
            :key="index"
            @click="changeIndex(index)"
          >
            <router-link
              :style="{ color: activeIndex == index ? '#33c0e1' : '#fff' }"
              :to="item.path"
              >{{ item.title }}</router-link
            >
          </div>
        </div>
        <div class="tabs-right">
          <div class="tab-item">
            <i
              class="el-icon-location"
              style="color: #438bd2; font-size: 16px"
            ></i>
            <span style="margin-left: 6px">{{$window.orientation}}</span>
          </div>
          <div class="tab-item">{{ nowDateTime }}</div>
          <div class="tab-item" @click="$router.push('/')">
            <i
              class="el-icon-s-home"
              style="color: #438bd2; font-size: 16px"
            ></i>
            <span style="margin-left: 6px">首页</span>
          </div>
        </div>
      </div>
    </div>
    <div class="data-main">
      <keep-alive>
        <router-view />
      </keep-alive>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nowDateTime: '', //显示时间的字段
      //控制选中状态的样式
      activeIndex: 0,
      // 渲染嵌套路由
      tabsData: [
        {
          index: 0,
          title: '收入分析',
          path: '/datadisplay/incomeanalysis',
        },
        {
          index: 1,
          title: '泊位利用',
          path: '/datadisplay/berthdata',
        },
        {
          index: 2,
          title: '车辆统计',
          path: '/datadisplay/vehicledata',
        },
        {
          index: 3,
          title: '设备统计',
          path: '/datadisplay/devicedata',
        },
      ],
    };
  },
  created() {
    this.currentTime();
  },
  methods: {
    changeIndex(index) {
      this.activeIndex = index;
    },
    // 展示实时的时间
    getTime() {
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? '0' + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? '0' + new Date().getSeconds()
          : new Date().getSeconds();
      this.nowDateTime =
        yy + '-' + mm + '-' + dd + ' ' + hh + ':' + mf + ':' + ss;
    },
    currentTime() {
      setInterval(this.getTime, 500);
    },
  },
};
</script>

<style scoped lang='less'>
.public-header {
  overflow: hidden;
  width: 100%;
  height: 10rem;
  background-color: #090622;
  .center-pos {
    position: relative;
    width: 50rem;
    height: 10rem;
    // background-color: orange;
    margin: auto;
    .text-center {
      height: 10rem;
      text-align: center;
      line-height: 10rem;
      font-size: 4rem;
      font-weight: 700;
      color: #4ea4f3;
    }
    .border-left {
      position: absolute;
      top: 5rem;
      right: 50rem;
    }
    .border-right {
      position: absolute;
      top: 5rem;
      left: 50rem;
    }
    .tabs-left,
    .tabs-right {
      position: absolute;
      top: 2rem;
      right: 70rem;
      width: 50rem;
      height: 5rem;
      font-size: 1.6rem;
      display: flex;
      justify-content: space-around;
      align-items: flex-end;
      // background-color: green;
      color: #fff;
      .tab-item {
        cursor: pointer;
        /* display: flex;
        align-items: center; */
        a {
          text-decoration: none;
          color: #fff;
        }
      }
    }
    .tabs-right {
      left: 70rem;
    }
  }
}
.data-main {
  width: 100%;
  height: calc(100vh - 10rem);
  background-color: #090622;
  overflow: hidden;
}
</style>